<template>
  <b-field horizontal :label="$t('endDateLabel')">
    <b-datepicker
      name="end_date"
      id="end_date"
      v-model="value.end_date"
      editable
      v-bind:required="isRequired"
      icon="calendar">
    </b-datepicker>
  </b-field>
</template>

<script>
export default {
  props: {
    isRequired: Boolean,
    value: {
      type: Object,
      required: true
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  }

}
</script>
