<template>
  <b-field horizontal :label="$t('messageLabel')">
    <b-input
      name="message"
      id="message"
      v-model="value.message"
      icon="info"
      v-bind:required="isRequired"
      placeholder="For client reference - this text will be displayed to client"
      validation-message="Please enter a valid reference - only dssfdsfd is allowed"
      pattern="[A-Za-z0-9\s.,?()]*">
    </b-input>
  </b-field>
</template>

<script>
export default {
  props: {
    isRequired: Boolean,
    value: {
      type: Object,
      required: true
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  }

}
</script>
