<template>
  <b-field horizontal :label="$t('missedAlertLabel')">
    <b-select
      name="use_alert"
      id="use_alert"
      v-model="value.alert_time"
      icon="bell"
      v-bind:required="isRequired"
      validation-message="Please choose an option">
        <option
          v-for="option in missedAlerts"
          :key = "option.value"
          :value="option.value">
            {{ option.name }}
        </option>
      </b-select>
    </b-field>
</template>

<script>

const missedAlerts = require('@/data/missed_alert_options.json')

export default {
  props: {
    isRequired: Boolean,
    value: {
      type: Object,
      required: true
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  data () {
    return {
      missedAlerts
    }
  }

}
</script>
