<template>
  <div class="medication">

    <div class="columns">
      <div class="column is-10 is-offset-1">
        <div class="box">
          <div>
            <h1>
              About
              <a class="button is-primary is-medium is-pulled-right" @click="showAddModal()">
                <span class="icon is-medium">
                  <i class="fa fa-plus"></i>
                </span>
                <span>Add New Medication</span>
              </a>
            </h1>
          </div>

          <!-- Show Scheduled Medication -->
          <table v-if="medications.length" class="table is-hoverable is-fullwidth is-striped">
            <thead>
              <tr>
              <!--  <th>id</th>
                <th>t id</th>
                <th>s id</th> -->
                <th>{{ $t('referenceLabel') }}</th>
                <th>{{ $t('instructionsLabel') }}</th>
                <th>{{ $t('promptTimeLabel') }}</th>
                <th>{{ $t('startDateLabel') }}</th>
                <th>{{ $t('endDateLabel') }}</th>
                <th>{{ $t('repeatLabel') }}</th>
                <th>{{ $t('missedAlertLabel') }}</th>
                <th>{{ $t('actionsLabel') }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(showmedication, index) in medications" :key="showmedication.id">
             <!--   <td><span :title="showmedication.id" >id</span></td>
                <td><span :title="showmedication.tablet_users_id" >id</span></td>
                <td><span :title="showmedication.scheduled_by" >id</span></td> -->
                <td>{{ showmedication.reference }} </td>
                <td>{{ showmedication.instruction }}</td>
                <td>{{ showmedication.prompt_time }} </td>
                <td>{{ showmedication.start_date }}</td>
                <td>{{ showmedication.end_date }} </td>
                <td>{{ showmedication.repeat }}</td>
                <td>{{ showmedication.alert_time }}</td>
                <td>
                  <div class="buttons">
                  <b-button type="is-success" icon-left="edit" @click="showEditModal(index)" title="Edit" ></b-button>
                  <b-button type="is-info" icon-left="copy" @click="showCopyModal(index)" title="Duplicate"></b-button>
                  <b-button type="is-danger" icon-left="trash" @click="showDeleteModal(index)" title="Delete"></b-button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>

        </div>
      </div> <!-- end Current medication list -->

      <!-- Modal to Add Medication -->
      <b-modal v-model="isModalVisible" scroll="keep" :can-cancel="false">
        <div class="modal-card" style="width:auto">
          <header class="modal-card-head">
            <p v-if="isAddModal" class="modal-card-title">Schedule New Medication</p>
            <p v-if="isCopyModal" class="modal-card-title">Schedule Duplicate Medication</p>
            <p v-if="isEditModal" class="modal-card-title">Edit Medication</p>
            <p v-if="isDeleteModal" class="modal-card-title">Delete Medication</p>
            <button class="delete" aria-label="close" @click="closeModal()"></button>
          </header>
          <section v-if="isDeleteModal" class="modal-card-body">
            <p>Are you sure you want to delete this medication?</p>
          </section>
          <section v-if="(isAddModal || isEditModal || isCopyModal)" class="modal-card-body">

            <!-- show errors -->
            <div v-if="errors.length > 0" class="message is-danger">
              <div class="message-body">
                <ul>
                  <li v-for="error in errors" :key="error.id">{{ error }}</li>
                </ul>
              </div>
            </div>

            <!-- show form -->
            <field-reference v-model="medication" is-required />

            <field-instructions v-model="medication" is-required />

            <field-start-date v-model="medication" is-required />

            <field-end-date v-model="medication" />

            <field-prompt-time v-model="medication" is-required />

            <field-repeat v-model="medication" is-required />

            <field-missed-alert v-model="medication" is-required />

          </section>

          <footer class="modal-card-foot">
            <button v-if="isAddModal || isCopyModal" class="button is-success" @click="addMedication">Add Medication</button>
            <button v-if="isEditModal" class="button is-success" @click="updateMedication">Save Changes</button>
            <button v-if="isDeleteModal" class="button is-danger" @click="deleteMedication">Delete Medication</button>
            <button class="button" @click="closeModal()">Cancel</button>
          </footer>
        </div>
       </b-modal><!-- end addMedicationModal -->

    </div>
  </div>
</template>

<script>

import FieldReference from '@/components/Fields/FieldReference.vue'
import FieldInstructions from '@/components/Fields/FieldInstructions.vue'
import FieldStartDate from '@/components/Fields/FieldStartDate.vue'
import FieldEndDate from '@/components/Fields/FieldEndDate.vue'
import FieldPromptTime from '@/components/Fields/FieldPromptTime.vue'
import FieldRepeat from '@/components/Fields/FieldRepeat.vue'
import FieldMissedAlert from '@/components/Fields/FieldMissedAlert.vue'

export default {

  components: {
    FieldReference,
    FieldInstructions,
    FieldStartDate,
    FieldEndDate,
    FieldPromptTime,
    FieldRepeat,
    FieldMissedAlert
  },

  data () {
    return {
      medications: [],
      medication: {},
      errors: [],
      isModalVisible: false,
      isAddModal: false,
      isEditModal: false,
      isCopyModal: false,
      isDeleteModal: false
    }
  },

  mounted () {
    this.getMedications()
  },

  methods: {

    closeModal () {
      this.medication.id = null
      this.medication.reference = ''
      this.medication.instruction = ''
      this.medication.start_date = null
      this.medication.end_date = null
      this.medication.prompt_time = null
      this.medication.repeat = ''
      this.medication.alert_time = null
      this.isModalVisible = false
      this.isAddModal = false
      this.isEditModal = false
      this.isCopyModal = false
      this.isDeleteModal = false
    },

    showAddModal () {
      this.medication.start_date = new Date()
      this.medication.alert_time = 1
      this.isAddModal = true
      this.isModalVisible = true
    },

    showEditModal (index) {
      this.medication.id = this.medications[index].id
      this.medication.reference = this.medications[index].reference
      this.medication.instruction = this.medications[index].instruction
      this.medication.repeat = this.medications[index].repeat
      this.medication.alert_time = this.medications[index].alert_time
      this.medication.start_date = new Date(this.medications[index].start_date)
      this.medication.end_date = this.medications[index].end_date ? new Date(this.medications[index].end_date) : null
      this.medication.prompt_time = new Date('1970-01-01T' + this.medications[index].prompt_time)
      this.isEditModal = true
      this.isModalVisible = true
    },

    showCopyModal (index) {
      this.medication.reference = this.medications[index].reference + ' (copy)'
      this.medication.instruction = this.medications[index].instruction
      this.medication.repeat = this.medications[index].repeat
      this.medication.alert_time = this.medications[index].alert_time
      this.medication.start_date = new Date(this.medications[index].start_date)
      this.medication.end_date = this.medications[index].end_date ? new Date(this.medications[index].end_date) : null
      this.medication.prompt_time = new Date('1970-01-01T' + this.medications[index].prompt_time)
      this.isCopyModal = true
      this.isModalVisible = true
    },

    showDeleteModal (index) {
      this.medication.id = this.medications[index].id
      this.isDeleteModal = true
      this.isModalVisible = true
    },

    // Get Scheduled Medications
    getMedications () {
      this.$http.get('medication_schedules')
        .then(response => {
          this.medications = response.data
        })
    },

    addMedication () {
      const mymedication = this.medication
      mymedication.prompt_time = mymedication.prompt_time.toLocaleString()
      console.log(mymedication.prompt_time)
      this.$http.post('medication_schedules', mymedication)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getMedications()
        })
        .catch(function (error) {
          console.log(error)
        })
    },

    updateMedication () {
      const mymedication = this.medication
      mymedication.prompt_time = mymedication.prompt_time.toLocaleString()
      this.$http.put('medication_schedules/' + mymedication.id, mymedication)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getMedications()
        })
        .catch(function (error) {
          console.log(error)
        })
    },

    deleteMedication () {
      const mymedicationid = this.medication.id
      this.$http.delete('medication_schedules/' + mymedicationid)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          // Remove from medications array
          const i = this.medications.map(item => item.id).indexOf(mymedicationid)
          this.medications.splice(i, 1)
        })
        .catch(function (error) {
          console.log(error)
        })
    }

  } // end methods

}
</script>
