<template>
  <div class="checkin">

    <div class="columns">
      <div class="column is-10 is-offset-1">
        <div class="box">
          <div>
            <h1>
              {{ $t('checkinTitle') }}
              <a class="button is-primary is-medium is-pulled-right" @click="showAddModal()">
                <span class="icon is-medium">
                  <i class="fa fa-plus"></i>
                </span>
                <span>Add New Check In</span>
              </a>
            </h1>
          </div>

          <!-- Show Scheduled CheckIn -->
          <table v-if="checkins.length" class="table is-hoverable is-fullwidth is-striped">
            <thead>
              <tr>
              <!--  <th>id</th>
                <th>t id</th>
                <th>s id</th> -->
                <th>{{ $t('referenceLabel') }}</th>
                <th>{{ $t('messageLabel') }}</th>
                <th>{{ $t('messageLabel') }}</th>
                <th>{{ $t('promptTimeLabel') }}</th>
                <th>{{ $t('startDateLabel') }}</th>
                <th>{{ $t('endDateLabel') }}</th>
                <th>{{ $t('repeatLabel') }}</th>
                <th>{{ $t('missedAlertLabel') }}</th>
                <th>{{ $t('actionsLabel') }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(showcheckin, index) in checkins" :key="showcheckin.id">
             <!--   <td><span :title="showcheckin.id" >id</span></td>
                <td><span :title="showcheckin.tablet_users_id" >id</span></td>
                <td><span :title="showcheckin.scheduled_by" >id</span></td> -->
                <td>{{ showcheckin.reference }} </td>
                <td>{{ showcheckin.message }}</td>
                <td>{{ showcheckin.prompt_1 }}</td>
                <td>{{ showcheckin.prompt_time }} </td>
                <td>{{ showcheckin.start_date }}</td>
                <td>{{ showcheckin.end_date }} </td>
                <td>{{ showcheckin.repeat }}</td>
                <td>{{ showcheckin.alert_time }}</td>
                <td>
                  <div class="buttons">
                  <b-button type="is-success" icon-left="edit" @click="showEditModal(index)" title="Edit" ></b-button>
                  <b-button type="is-info" icon-left="copy" @click="showCopyModal(index)" title="Duplicate"></b-button>
                  <b-button type="is-danger" icon-left="trash" @click="showDeleteModal(index)" title="Delete"></b-button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>

        </div>
      </div> <!-- end Current checkin list -->

      <!-- Modal to Add CheckIn -->
      <b-modal v-model="isModalVisible" scroll="keep" :can-cancel="false">
        <div class="modal-card" style="width:auto">
          <header class="modal-card-head">
            <p v-if="isAddModal" class="modal-card-title">Schedule New CheckIn</p>
            <p v-if="isCopyModal" class="modal-card-title">Schedule Duplicate CheckIn</p>
            <p v-if="isEditModal" class="modal-card-title">Edit CheckIn</p>
            <p v-if="isDeleteModal" class="modal-card-title">Delete CheckIn</p>
            <button class="delete" aria-label="close" @click="closeModal()"></button>
          </header>
          <section v-if="isDeleteModal" class="modal-card-body">
            <p>Are you sure you want to delete this check in?</p>
          </section>
          <section v-if="(isAddModal || isEditModal || isCopyModal)" class="modal-card-body">
            <!-- show errors -->
            <div v-if="errors.length > 0" class="message is-danger">
              <div class="message-body">
                <ul>
                  <li v-for="error in errors" :key="error.id">{{ error }}</li>
                </ul>
              </div>
            </div>

            <!-- show form -->
            <field-reference v-model="checkin" is-required />

            <field-message v-model="checkin" />

            <field-prompt v-model="checkin" />

            <field-start-date v-model="checkin" is-required />

            <field-end-date v-model="checkin" />

            <field-prompt-time v-model="checkin" is-required />

            <field-repeat v-model="checkin" is-required />

            <field-missed-alert v-model="checkin" is-required />

          </section>

          <footer class="modal-card-foot">
            <button v-if="isAddModal || isCopyModal" class="button is-success" @click="addCheckIn">Add CheckIn</button>
            <button v-if="isEditModal" class="button is-success" @click="updateCheckIn">Save Changes</button>
            <button v-if="isDeleteModal" class="button is-danger" @click="deleteCheckIn">Delete CheckIn</button>
            <button class="button" @click="closeModal()">Cancel</button>
          </footer>
        </div>
       </b-modal><!-- end addCheckInModal -->

    </div>
  </div>
</template>

<script>

import FieldReference from '@/components/Fields/FieldReference.vue'
import FieldMessage from '@/components/Fields/FieldMessage.vue'
import FieldPrompt from '@/components/Fields/FieldPrompt.vue'
import FieldStartDate from '@/components/Fields/FieldStartDate.vue'
import FieldEndDate from '@/components/Fields/FieldEndDate.vue'
import FieldPromptTime from '@/components/Fields/FieldPromptTime.vue'
import FieldRepeat from '@/components/Fields/FieldRepeat.vue'
import FieldMissedAlert from '@/components/Fields/FieldMissedAlert.vue'

export default {

  components: {
    FieldReference,
    FieldMessage,
    FieldPrompt,
    FieldStartDate,
    FieldEndDate,
    FieldPromptTime,
    FieldRepeat,
    FieldMissedAlert
  },

  data () {
    return {
      checkins: [],
      checkin: {},
      errors: [],
      isModalVisible: false,
      isAddModal: false,
      isEditModal: false,
      isCopyModal: false,
      isDeleteModal: false
    }
  },

  mounted () {
    this.getCheckIns()
  },

  methods: {

    closeModal () {
      this.checkin.id = null
      this.checkin.reference = ''
      this.checkin.message = ''
      this.checkin.prompt_1 = ''
      this.checkin.start_date = null
      this.checkin.end_date = null
      this.checkin.prompt_time = null
      this.checkin.repeat = ''
      this.checkin.alert_time = null
      this.isModalVisible = false
      this.isAddModal = false
      this.isEditModal = false
      this.isCopyModal = false
      this.isDeleteModal = false
    },

    showAddModal () {
      this.checkin.start_date = new Date()
      this.checkin.alert_time = 1
      this.isAddModal = true
      this.isModalVisible = true
    },

    showEditModal (index) {
      this.checkin.id = this.checkins[index].id
      this.checkin.reference = this.checkins[index].reference
      this.checkin.message = this.checkins[index].message
      this.checkin.prompt_1 = this.checkins[index].prompt_1
      this.checkin.repeat = this.checkins[index].repeat
      this.checkin.alert_time = this.checkins[index].alert_time
      this.checkin.start_date = new Date(this.checkins[index].start_date)
      this.checkin.end_date = this.checkins[index].end_date ? new Date(this.checkins[index].end_date) : null
      this.checkin.prompt_time = new Date('1970-01-01T' + this.checkins[index].prompt_time)
      this.isEditModal = true
      this.isModalVisible = true
    },

    showCopyModal (index) {
      this.checkin.reference = this.checkins[index].reference + ' (copy)'
      this.checkin.message = this.checkins[index].message
      this.checkin.prompt_1 = this.checkins[index].prompt_1
      this.checkin.repeat = this.checkins[index].repeat
      this.checkin.alert_time = this.checkins[index].alert_time
      this.checkin.start_date = new Date(this.checkins[index].start_date)
      this.checkin.end_date = this.checkins[index].end_date ? new Date(this.checkins[index].end_date) : null
      this.checkin.prompt_time = new Date('1970-01-01T' + this.checkins[index].prompt_time)
      this.isCopyModal = true
      this.isModalVisible = true
    },

    showDeleteModal (index) {
      this.checkin.id = this.checkins[index].id
      this.isDeleteModal = true
      this.isModalVisible = true
    },

    // Get Scheduled CheckIns
    getCheckIns () {
      this.$http.get('check_in_schedules')
        .then(response => {
          this.checkins = response.data
        })
    },

    addCheckIn () {
      const mycheckin = this.checkin
      mycheckin.prompt_time = mycheckin.prompt_time.toLocaleString()
      console.log(mycheckin.prompt_time)
      this.$http.post('check_in_schedules', mycheckin)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getCheckIns()
        })
        .catch(function (error) {
          console.log(error)
        })
    },

    updateCheckIn () {
      const mycheckin = this.checkin
      mycheckin.prompt_time = mycheckin.prompt_time.toLocaleString()
      this.$http.put('check_in_schedules/' + mycheckin.id, mycheckin)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getCheckIns()
        })
        .catch(function (error) {
          console.log(error)
        })
    },

    deleteCheckIn () {
      const mycheckinid = this.checkin.id
      this.$http.delete('check_in_schedules/' + mycheckinid)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getCheckIns()
        })
        .catch(function (error) {
          console.log(error)
        })
    }

  } // end methods

}
</script>
