<template>
  <b-field horizontal :label="$t('repeatLabel')">
    <b-select
      name="repeat"
      id="repeat"
      v-model="value.repeat"
      icon="repeat"
      expanded
      v-bind:required="isRequired"
      validation-message="Please choose a repeat option">
        <option
          v-for="option in repeats"
          :key = "option.value"
          :value="option.value">
            {{ option.name }}
        </option>
      </b-select>
    </b-field>
</template>

<script>

const repeats = require('@/data/repeat_options.json')

export default {
  props: {
    isRequired: Boolean,
    value: {
      type: Object,
      required: true
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  data () {
    return {
      repeats
    }
  }

}
</script>
