<template>
   <b-field horizontal :label="$t('startDateLabel')">
    <b-datepicker
      name="start_date"
      id="start_date"
      v-model="value.start_date"
      editable
      v-bind:required="isRequired"
      icon="calendar"
      validation-message="Please choose a start date">
    </b-datepicker>
  </b-field>
</template>

<script>
export default {
  props: {
    isRequired: Boolean,
    value: {
      type: Object,
      required: true
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  }

}
</script>
