<template>
  <b-field horizontal :label="$t('promptTimeLabel')">

    <b-timepicker
      name="prompt_time"
      id="prompt_time"
      v-model="value.prompt_time"
      :hour-format="hourFormat"
      editable
      v-bind:required="isRequired"
      validation-message="Please choose a prompt time"
      :incrementMinutes="5"
      icon="clock-o">
    </b-timepicker>

    <b-field>
      <b-radio-button
        v-model="hourFormat"
        name="12"
        native-value="12">
        12 hr
      </b-radio-button>
      <b-radio-button
        v-model="hourFormat"
        name="24"
        native-value="24">
        24 hr
      </b-radio-button>
    </b-field>

  </b-field>
</template>

<script>
export default {
  props: {
    isRequired: Boolean,
    value: {
      type: Object,
      required: true
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  data () {
    return {
      hourFormat: '12'
    }
  }

}
</script>
